.headlineRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 40px;

  @media (max-width: 900px) {
    flex-direction: column;
    .search {
      margin-top: 10px;
    }
  }

  //@media (max-width: 500px) {
  //  flex-direction: column !important;
  //  .search {
  //    margin-top: 10px;
  //  }
  //}

  .search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #F3F3F3;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 10px;

    input {
      border: none;
      outline: none;
      margin-left: 10px;
      color: #5c5c5c;

      &::placeholder  {
        font-size: 0.875rem;
        line-height: 1.0625rem;
        color: #C4C4C4;
      }
    }
  }
}

.clearOrder {
  border: none;
  background: none;
  transition: all 0.2s ease-in-out;

  @media (max-width: 900px) {
    margin-top: 30px;
  }

  &:not([disabled]):hover {
    opacity: 0.8;
  }
  &:not([disabled]):active {
    opacity: 0.6;
  }
}
